/**
 * Object
 */
gltfLoader.load(
    'websiteC.glb',
    (gltf) => 
    {
        gltf.scene.traverse((child) =>
        {
            
            child.material = bakedMaterial

            //character 
            if (child.name === 'humanA')
            {
                humanA.instance = child
            }
        })

        const alienCarLightMesh = gltf.scene.children.find((child) => child.name === 'alienCarLight')
        const  portalLightMesh= gltf.scene.children.find((child) => child.name === 'portalLight')

        alienCarLightMesh.material = alienCarLightMaterial
        portalLightMesh.material = portalLightMaterial

        scene.add(gltf.scene)
    }
)


/**
 * fireflyes
 */
//geometrie 
const firefliesGeometry = new THREE.BufferGeometry()
const firefliesCount = 300
const positionArray = new Float32Array(firefliesCount * 3)
const scaleArray = new Float32Array(firefliesCount)


for(let i = 0; i < firefliesCount; i++)
{
    positionArray[i * 3 + 0] = Math.random() * 22            //wight
    positionArray[i * 3 + 1] = Math.random() * 4             //hight
    positionArray[i * 3 + 2] = (Math.random() - 1) * 20      //position 

    scaleArray[i] = Math.random()
}

firefliesGeometry.setAttribute('position', new THREE.BufferAttribute(positionArray, 3))
firefliesGeometry.setAttribute('aScale', new THREE.BufferAttribute(scaleArray, 1))

//fireflies material
const firefliesMaterial = new THREE.ShaderMaterial({ 
    uniforms:{
        uTime: { value: 0 },
        uPixelRatio: { value: Math.min(window.devicePixelRatio, 2) },
        uSize: { value: 150 },
    },
    vertexShader: firefliesVertexShader,
    fragmentShader: firefliesFragmentShader,
    transparent: true,
    blending: THREE.AdditiveBlending,
    depthWrite: false,
 })

//  gui.add(firefliesMaterial.uniforms.uSize, 'value').min(0).max(500).step(1).name('firefliesSize')


// Points
const fireflies = new THREE.Points(firefliesGeometry, firefliesMaterial)
fireflies.position.z = 10
fireflies.position.x = - 10
scene.add(fireflies)


/**
 * Character humanA
 */
let humanA = {
    instance: null,
    moveDistance: 1, 
    jumpHeight: 1,
    isMoving: false,
    moveDuration: 0.2, 
}



/**
 * Sizes
 */
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

window.addEventListener('resize', () =>
{
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))  

})


