/**
 * austronaut movement
 */
function moveHumanA(targetPosition, targetRotation)
    {
        humanA.isMoving = true;
        const t1 = gsap.timeline({
            onComplete: () => {
                humanA.isMoving = false;
            }
        });

        //position
        t1.to(humanA.instance.position,
            {
                x: targetPosition.x,
                z: targetPosition.z,
                duration: humanA.moveDuration, 
            });

        //rotation
        t1.to(humanA.instance.rotation,
            {
                y: targetRotation,
                duration: humanA.moveDuration, 
            }, 
            0
            );
        
        //jump
        t1.to(humanA.instance.position,
             {
                y: humanA.instance.position.y + humanA.jumpHeight,
                duration: humanA.moveDuration / 2, 
                yoyo: true,
                repeat: 1,

            }, 
            0
            );
    }

//motion
function oneKeyDown(event){

    if(humanA.isMoving) return; 

    const targetPosition = new THREE.Vector3().copy(humanA.instance.position); 
    let targetRotation = 0; 

    switch(event.key.toLowerCase()){
        case "s":
            case "arrowdown":
                targetPosition.z += humanA.moveDistance;
                targetRotation = 0;
                break;

        case "w":
            case "arrowup":
                targetPosition.z -= humanA.moveDistance;
                targetRotation = 0;
                break;

        case "d":
            case "arrowright":
                targetPosition.x += humanA.moveDistance;
                targetRotation = 0 / 1;
                break;

        case "a":
            case "arrowleft":
                targetPosition.x -= humanA.moveDistance;
                targetRotation = - 0 / 1;
                break;

            default:
                return;
    }

    moveHumanA(targetPosition, targetRotation);
}
window.addEventListener("keydown", oneKeyDown)


/**
 * Camera
 */
const camera = new THREE.PerspectiveCamera(45, sizes.width / sizes.height, 0.1, 100)
camera.position.x = 0 //diese zahl ndern bei click
camera.position.y = 3.2
camera.position.z = 12

console.log(camera.position)

const cameraOffset = new THREE.Vector3(0, 3.2, 12);
camera.zoom = 3;
camera.updateProjectionMatrix();

scene.add(camera)


// axes Helper
const axesHelper = new THREE.AxesHelper( 5 );
scene.add( axesHelper );

// Controls
const controls = new OrbitControls(camera, canvas)
controls.enableDamping = true



/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas,
    antialias: true
})
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))

//debug background color
debugObject.clearColor = '#9c3002'
renderer.setClearColor(debugObject.clearColor)
